import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { login } from './../../../store/actions/auth'

import { useHistory } from 'react-router-dom'

const Login = () => {
  // debugger
  const initialState = {
    mobile: '',
    passcode: '',
    isLoading: false,
    errorMessage: null,
    success: false
  }

  const [data, setData] = React.useState(initialState)

  const history = useHistory()

  useSelector(state => {
    if (state && state.auth.login.data) {
      history.push('/health')
    }
  })


  const errorMessage = useSelector(state => state.auth.login.error)

  const dispatch = useDispatch()

  const handleInputChange = event => {
    setData({
      ...data,
      [event.target.name]: event.target.value
    })
  }

  const handleSubmit = event => {
    event.preventDefault()
    setData({
      ...data,
      isLoading: true,
      errorMessage: null
    })
    dispatch(login({ mobile: data.mobile, passcode: data.passcode }))
  }
  return (
    <>
      <div className='limiter'>
        <div className='container-login100'>
          <div className='wrap-login100 p-t-50 p-b-90'>
            <form
              className='login100-form validate-form flex-sb flex-w'
              onSubmit={handleSubmit}
            >
              <span className='login100-form-title p-b-51'>Please LogIn</span>
              {errorMessage ? (
                <div className='alert alert-warning input100 alert-dismissible  show'>
                  <h4 className='alert-heading'>
                    <i className='fa fa-warning'></i> Warning!
                  </h4>
                  <p>{errorMessage&&errorMessage.message?errorMessage.message:'Login Failed'}</p>
                  <button type='button' className='close' data-dismiss='alert'>
                    &times;
                  </button>
                </div>
              ) : (
                <div></div>
              )}
              <div
                className='wrap-input100 validate-input m-b-16'
                data-validate='Username is required'
              >
                <input
                  className='input100'
                  type='text'
                  name='mobile'
                  onChange={handleInputChange}
                  value={data.mobile}
                  placeholder='Mobile Number'
                />
                <span className='focus-input100'></span>
              </div>

              <div
                className='wrap-input100 validate-input m-b-16'
                data-validate='Password is required'
              >
                <input
                  className='input100'
                  type='password'
                  name='passcode'
                  onChange={handleInputChange}
                  value={data.passcode}
                  placeholder='Access Code'
                />
                <span className='focus-input100'></span>
              </div>

              <div className='flex-sb-m w-full p-t-3 p-b-24 '>
                <div className='contact100-form-checkbox'>
                  <input
                    className='input-checkbox100'
                    id='ckb1'
                    type='checkbox'
                    name='remember-me'
                  />
                  <label className='label-checkbox100' htmlFor='ckb1'>
                    Remember me
                  </label>
                </div>
              </div>
              <div className='container-login100-form-btn m-t-17'>
                <button className='login100-form-btn'>
                  {/* <Link to="/Health" style={{ color: '#ffff' }}> */}
                  LogIn
                  {/* </Link> */}
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div id='dropDownSelect1'></div>
    </>
  )
}

export default Login
