import React, { useEffect, useState } from 'react'
import './Health.css'
import healthImage from './../../../assets/img/health.svg'
import allergyPic from './../../../assets/img/allergy.svg'
import treatment from './../../../assets/img/treatment.svg'
import imagingPic from './../../../assets/img/imaging.svg'
import family from './../../../assets/img/family.svg'
import lifestyle from './../../../assets/img/lifestyle.svg'
import { getHealth } from './../../../store/actions/health'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons'




import Header from '../../layout/Header/Header'

import { useSelector, useDispatch } from 'react-redux'

export const Health = () => {
  const dispatch = useDispatch()

  const [isFamilyHistoryMore, setisFamilyHistoryMore] = useState(false)
  // const [Allergy, setAllergy] = useState(false)
  // const [active, setActive] = useState(false)
  // const [Imaging, setImaging] = useState(false)
  const [Lifestyle, setLifestyle] = useState(false)
  const [Health, setHealth] = useState(false)


  useEffect(() => {
    dispatch(getHealth({ id: localStorage.getItem('PAU') }))
  }, [])

  const health = useSelector(state => {
    if (state && state.health.health.data) {
      return state.health.health.data
    }
  })



  const errorMessage = useSelector(state => {
    if (state && state.health.health.error !== null && state.health.health.error !== undefined) {
      return state.health.health.error
    }
  })



  const { health_trends, allergy, active_discontinued_treatments, imaging, risk_factors } = health && health.health_record ? health.health_record : {}

  return (
    <div className='Health'>
      <Header />

      <section>
        <div className='container'>

          {errorMessage ?
            <div style={{ marginTop: '80px' }}>
              <div className='alert alert-warning input100 alert-dismissible  show'>
                <h4 className='alert-heading'>
                  <FontAwesomeIcon icon={faExclamationTriangle} /> Warning!{' '}
                </h4>
                <p style={{ marginTop: '0px' }}>{errorMessage ? errorMessage.message : 'matching records found'}</p>
                <button
                  type='button'
                  className='close pdg-set-2'
                  data-dismiss='alert'
                >
                  &times;
            </button>
              </div>
            </div>
            : null}

          <div className='row pt-5'>
            <div className='col-lg-4'>
              <div>
                <img src={healthImage} width='60%' alt='' />
              </div>
            </div>
            <div className='col-lg-8 content-box'>
              <h3 className='heading pt-5'>Health Treands</h3>
              {health_trends &&
                health_trends.length < 200 ? (
                  <div className='content pt-2'>
                    {health_trends &&
                      health_trends
                      ? health_trends
                      : 'No Active Health Trends'}
                  </div>
                ) : (
                  <div>
                    {Health === false ? (
                      <div className='content pt-2'>
                        {health_trends
                          ? health_trends.slice(0, 200)
                          : 'No Active Health Trends'}
                      </div>
                    ) : (
                        <div className='content pt-2'>
                          {
                            health_trends
                              ? health_trends
                              : 'No Active Health Trends'}
                        </div>
                      )}

                    <div className='pt-3'>
                      <button
                        type='button'
                        onClick={() =>
                          setHealth(!Health)
                        }
                        className=' btn btn-primary btn-set-3'
                      >
                        {Health ? 'Less' : 'More'}
                      </button>
                    </div>
                  </div>
                )}
            </div>
          </div>
        </div>
      </section>

      <section>
        <div className='container '>
          <div className='row pt-5'>
            <div className='col-lg-8 content-box'>
              <h3 className='heading pt-5'>Allergy Information</h3>
              <div className='content pt-2'>
                {allergy && allergy.medicinal ? allergy.medicinal : 'No Active Medicinal'}
              </div>
              <div className='content pt-2'>
                {allergy && allergy.food ? allergy.food : 'No Active Food'}
              </div>
              <div className='content pt-2'>
                {allergy && allergy.sensitivity ? allergy.sensitivity : 'No Active Sensitivity'}
              </div>
              <div className='pt-3'>
                {/* <button type='button' className=' btn btn-primary btn-set-3'>
                  More
                </button> */}
              </div>
            </div>
            <div className='col-lg-4  '>
              <div>
                <img src={allergyPic} width='60%' alt='' />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div className='container'>
          <div className='row pt-5'>
            <div className='col-lg-4 '>
              <div>
                <img src={treatment} width='60%' alt='' />
              </div>
            </div>
            <div className='col-lg-8 content-box'>
              <h3 className='heading pt-5'>Active and past treatments</h3>
              <div className='content pt-2'>
                {active_discontinued_treatments && active_discontinued_treatments.active ? active_discontinued_treatments.active : 'No Active treatments'}
              </div>
              <div className='content pt-2'>
                {active_discontinued_treatments &&
                  active_discontinued_treatments.past
                  ? active_discontinued_treatments.past
                  : 'No past treatments'}
              </div>
              <div className='content pt-2'>
                {active_discontinued_treatments &&
                  active_discontinued_treatments
                    .repeat_prescription
                  ? active_discontinued_treatments
                    .repeat_prescription
                  : 'No past repeat prescription'}
              </div>
              <div className='content pt-2'>
                {active_discontinued_treatments &&
                  active_discontinued_treatments
                    .change_of_drug
                  ? active_discontinued_treatments
                    .change_of_drug
                  : 'No past drug'}
              </div>
              <div className='content pt-2'>
                {active_discontinued_treatments &&
                  active_discontinued_treatments.other
                  ? active_discontinued_treatments.other
                  : 'No other treatment'}
              </div>
              <div className='pt-3'>
                {/* <button type='button' className=' btn btn-primary btn-set-3'>
                  More
                </button> */}
              </div>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div className='container '>
          <div className='row pt-5'>
            <div className='col-lg-8 content-box'>
              <h3 className='heading pt-5'>Imaging</h3>
              <div className='content pt-2'>
                {imaging && imaging.active
                  ? imaging.active
                  : 'No Active imaging'}
              </div>
              <div className='content pt-2'>
                {imaging && imaging.past
                  ? imaging.past
                  : 'No past imaging'}
              </div>
              <div className='content pt-2'>
                {imaging && imaging.event
                  ? imaging.event
                  : 'No Active event'}
              </div>
              <div className='content pt-2'>
                {imaging && imaging.deformity
                  ? imaging.deformity
                  : 'No Active deformity'}
              </div>
              <div className='pt-3'>
                {/* <button type='button' className='btn btn-primary btn-set-3'>
                  More
                </button> */}
              </div>
            </div>
            <div className='col-lg-4  '>
              <div>
                <img src={imagingPic} width='60%' alt='' />
              </div>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div className='container'>
          <div className='row pt-5'>
            <div className='col-lg-4 '>
              <div>
                <img src={family} width='60%' alt='' />
              </div>
            </div>
            <div className='col-lg-8 content-box'>
              <h3 className='heading pt-5'>Family History</h3>
              {risk_factors &&
                risk_factors.family_history &&
                risk_factors.family_history.length < 250 ? (
                  <div className='content pt-2'>
                    {risk_factors &&
                      risk_factors.family_history
                      ? risk_factors.family_history
                      : 'No Active Family History'}
                  </div>
                ) : (
                  <div>
                    {isFamilyHistoryMore === false ? (
                      <div className='content pt-2'>
                        {risk_factors &&
                          risk_factors.family_history
                          ? risk_factors.family_history.slice(0, 2)
                          : 'No Active Family History'}
                      </div>
                    ) : (
                        <div className='content pt-2'>
                          {risk_factors &&
                            risk_factors.family_history
                            ? risk_factors.family_history
                            : 'No Active Family History'}
                        </div>
                      )}

                    <div className='pt-3'>
                      <button
                        type='button'
                        onClick={() =>
                          setisFamilyHistoryMore(!isFamilyHistoryMore)
                        }
                        className=' btn btn-primary btn-set-3'
                      >
                        {isFamilyHistoryMore ? 'Less' : 'More'}
                      </button>
                    </div>
                  </div>
                )}
            </div>
          </div>
        </div>
      </section>




      <section>
        <div className='container '>
          <div className='row pt-5'>
            <div className='col-lg-8 content-box'>
              <h3 className='heading pt-5'>Lifestyle</h3>
              <div className='content pt-2'>
                {risk_factors &&
                  risk_factors.lifestyle &&
                  risk_factors.lifestyle.length < 200 ? (
                    <div className='content pt-2'>
                      {risk_factors &&
                        risk_factors.lifestyle
                        ? risk_factors.lifestyle
                        : 'No Active lifestyle'}
                    </div>
                  ) : (
                    <div>
                      {Lifestyle === false ? (
                        <div className='content pt-2'>
                          {risk_factors &&
                            risk_factors.lifestyle
                            ? risk_factors.lifestyle.slice(0, 200)
                            : 'No Active lifestyle'}
                        </div>
                      ) : (
                          <div className='content pt-2'>
                            {risk_factors &&
                              risk_factors.lifestyle
                              ? risk_factors.lifestyle
                              : 'No Active lifestyle'}
                          </div>
                        )}

                      <div className='pt-3'>
                        <button
                          type='button'
                          onClick={() =>
                            setLifestyle(!Lifestyle)
                          }
                          className=' btn btn-primary btn-set-3'
                        >
                          {lifestyle ? 'Less' : 'More'}
                        </button>
                      </div>
                    </div>
                  )}
              </div>

            </div>
            <div className='col-lg-4 pb-5'>
              <div className='pb-5'>
                <img src={lifestyle} width='60%' alt='' />
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}

export default Health
